
.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;
    padding: 24px 80px;
    /* потом поменя 80 на 0 и поставить паддинги главному компоненту */
}

.logo {
    display: flex;
    gap: 20.75px;
    align-items: center;
    cursor: pointer;
}

.logo svg {
    height: 32px;
    width: 32px;
}

.logo span {
    font-size: 24px;
    font-weight: 700;
}

.nav {
    display: flex;
    gap: 40px;
    font-size: 24px;
}

.nav a {
    text-decoration: none;
    color: inherit; 
    /* потом поменять цвет */
}

.header__seach {
    padding: 12px 16px;
    background-color: #393B3C;
    border-radius: 8px;
    display: flex;
    align-items: center;
    gap: 12px;
}

.header__input {
    width: 550px;
    background: inherit;
    outline: none;
    border: none;
    font-size: 18px;
    color: white;
    /* заменить цвет на константу */
}

.header__authBtn {
    font-size: 24px;
    border: none;
    background: none;
    cursor: pointer;
    color: white;
}